import React from 'react'
import { Cursor, useTypewriter } from 'react-simple-typewriter'
import { SocialIcon } from 'react-social-icons'
import { Carousel } from 'react-bootstrap'
import template from "./../../../template.jpg"

function Home() {
  const [text, count] = useTypewriter({
    words: [
      "MrSan",
      "Developer",
      "Designer"
    ],
    loop: true,
    delaySpeed: 1500
  })
  return (
    <main>
      <div className="container">
        <div className="left-content">
          <div className="hero-text">
            <h3>Hi, There!</h3>
            <h1>I Am <br/><span>{text}</span><Cursor cursorColor="#3A015C" /></h1>
            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Debitis, molestiae! Architecto id tempora quo doloremque ducimus laboriosam, rerum placeat, amet possimus, ea dolor dignissimos voluptatem accusamus nobis nisi assumenda quod?</p>
          </div>
          <div className="social">
            <SocialIcon style={{width: "30px", height: "30px"}} url="https://facebook.com/"></SocialIcon>
            <SocialIcon style={{width: "30px", height: "30px"}} url="https://instagram.com/"></SocialIcon>
            <SocialIcon style={{width: "30px", height: "30px"}} url="https://twitter.com/"></SocialIcon>
          </div>
          <button type="button">More About Me</button>
        </div>
        <div className="right-content">
        <Carousel>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={template}
          alt="First slide"
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={template}
          alt="Second slide"
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={template}
          alt="Third slide"
        />
      </Carousel.Item>
    </Carousel>
        </div>
      </div>
    </main>
  )
}

export default Home