import { useState } from "react"
import { motion } from 'framer-motion'

function NavigationBar() {
  const [active, setActive] = useState("nav-menu");
  const [toggleIcon, setToggleIcon] = useState("nav-toggler");

  const navToggle = () => {
    active === "nav-menu" ? setActive("nav-menu nav-active"): setActive("nav-menu");
    toggleIcon === "nav-toggler" ? setToggleIcon("nav-toggler toggle") : setToggleIcon("nav-toggler")
  }


  return (
    <header>
        <motion.div className="nav-brand"
        initial={{
          y: -150,
          opacity: 0,
          scale: .5
        }}
        animate={{
          y: 0,
          opacity: 1,
          scale: 1
        }}
        transition={{
          duration: .75,
        }}>
            <h3>Portfolio</h3>
        </motion.div>

        <nav className="nav">
              <ul className={active}>
                <motion.li
                initial={{
                  opacity: 0,
                  scale: .75
                }}
                animate={{
                  opacity: 1,
                  scale: 1
                }}
                transition={{
                  duration: .75
                }}
                className="nav-item"><a href='#main' className="nav-link">Home</a></motion.li>
                <motion.li
                initial={{
                  opacity: 0,
                  scale: .75
                }}
                animate={{
                  opacity: 1,
                  scale: 1
                }}
                transition={{
                  duration: .75
                }}
                className="nav-item"><a href='#contact' className="nav-link">Contact</a></motion.li>
                <motion.li
                initial={{
                  opacity: 0,
                  scale: .75
                }}
                animate={{
                  opacity: 1,
                  scale: 1
                }}
                transition={{
                  duration: .75
                }}
                className="nav-item"><a href='#about' className="nav-link">About</a></motion.li>
              </ul>
              <motion.div
              initial={{
                opacity: 0,
                scale: 1.5
              }}
              animate={{
                opacity: 1,
                scale: 1
              }}
              transition={{
                duration: .75
              }}
              role="button" onClick={navToggle} className={toggleIcon}>
                <div role="button" className="line1"></div>
                <div role="button" className="line2"></div>
                <div role="button" className="line3"></div>
              </motion.div>
        </nav>
    </header>
  )
}

export default NavigationBar